<template>
  <div>
    <br />
    <h1 class="my-5">EdiciÃ³n de tatuaje ðŸ› </h1>

    <div class="d-flex flex-column align-items-center">
      <form @submit.prevent="actualizarTatuaje(tatuaje)">
        <div class="row d-flex align-items-center justify-content-center">
          <div
            id="form"
            class="col d-flex flex-column justify-content-evenly my-2 px-3"
          >
            <h3 class="mt-3">Tatuaje ðŸ’‰</h3>
            <input
              type="text"
              class="form-control my-2"
              placeholder="Nombre"
              v-model="tatuaje.nombre"
              required
            />
            <div class="d-flex justify-content-center mt-3">
              <p>Â¿Lo has tatuado ya?</p>
              <div class="form-check mx-5">
                <input
                  class="form-check-input"
                  type="radio"
                  name="flexRadioDefault"
                  id="noTatuado"
                  value="false"
                  checked
                  v-model="tatuaje.tatuado"
                  required
                />
                <label class="form-check-label" for="noTatuado">No</label>
              </div>
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="radio"
                  name="flexRadioDefault"
                  id="siTatuado"
                  value="true"
                  v-model="tatuaje.tatuado"
                  required
                />
                <label class="form-check-label" for="siTatuado">Si</label>
              </div>
            </div>
            <div class="input-group mb-2 d-flex align-items-center">
              <span class="mx-2">Autores</span>
              <select
                v-model="tatuaje.autor"
                id="seleccion"
                class="form-control"
              ></select>
            </div>
            <div class="input-group d-flex align-items-center">
              <div class="input-group-prepend">
                <span id="apend" class="input-group-text">â‚¬</span>
              </div>
              <input
                type="text"
                class="form-control my-2"
                placeholder="Precio"
                v-model="tatuaje.precio"
                required
              />
            </div>
            <textarea
              class="form-control my-2"
              placeholder="DescripciÃ³n"
              v-model="tatuaje.aboutTattoo"
              required
            />
          </div>

          <div class="col my-2">
            <form enctype="multipart/form-data">
              <div class="drop-container">
                <div class="drop">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="60"
                    height="60"
                    fill="currentColor"
                    class="bi bi-images"
                    viewBox="0 0 16 16"
                  >
                    <path d="M4.502 9a1.5 1.5 0 1 0 0-3 1.5 1.5 0 0 0 0 3z" />
                    <path
                      d="M14.002 13a2 2 0 0 1-2 2h-10a2 2 0 0 1-2-2V5A2 2 0 0 1 2 3a2 2 0 0 1 2-2h10a2 2 0 0 1 2 2v8a2 2 0 0 1-1.998 2zM14 2H4a1 1 0 0 0-1 1h9.002a2 2 0 0 1 2 2v7A1 1 0 0 0 15 11V3a1 1 0 0 0-1-1zM2.002 4a1 1 0 0 0-1 1v8l2.646-2.354a.5.5 0 0 1 .63-.062l2.66 1.773 3.71-3.71a.5.5 0 0 1 .577-.094l1.777 1.947V5a1 1 0 0 0-1-1h-10z"
                    />
                  </svg>
                  <span class="text">Arrastra tus fotos aqui! ðŸ‘‡</span>
                  <div class="or-con">
                    <span class="line"></span>
                    <span class="or">O</span>
                    <span class="line"></span>
                  </div>
                  <label for="file-upload">Busca tus fotos</label>
                  <input
                    type="file"
                    name=""
                    id="file-upload"
                    class="file-input"
                    @change="subirFile"
                  />
                </div>
              </div>
            </form>
          </div>
        </div>
        <div class="row">
          <b-button id="enviar" pill class="my-4" type="submit">
            Editar
          </b-button>
        </div>
      </form>
    </div>

    <div class="container">
      <b-alert
        :show="dismissCountDown"
        :variant="mensaje.color"
        @dismissed="dismissCountDown = 0"
        @dismiss-count-down="countDownChanged"
      >
        <p>{{ mensaje.texto }}</p>
        <b-progress
          :variant="mensaje.color"
          :max="dismissSecs"
          :value="dismissCountDown"
          height="4px"
        ></b-progress>
      </b-alert>
    </div>

    <div
      id="mostrarImagenes"
      class="container d-flex flex-wrap justify-content-center"
    >
      <div
        class="cajaTattoo"
        v-for="(item, index) in tatuaje.imagenes"
        :key="index"
      >
        <img class="imagenTattoo" :src="src + item" alt="tattoo img" />
        <img @click="showMsgBox(item)" class="xTattoo" src="X.png" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      box: '',
      src: 'https://tattoo-backend.vercel.app/',
      tatuajes: [],
      dismissSecs: 5,
      dismissCountDown: 0,
      showDismissibleAlert: false,
      mensaje: {
        color: '',
        texto: '',
      },
      tatuaje: {
        nombre: '',
        tatuado: Boolean,
        autor: '',
        imagenes: Array(),
        imagen: null,
        precio: 0,
        aboutTattoo: '',
      },
      autores: [],
      files: [],
    }
  },
  created() {
    if (
      localStorage.getItem('tatuador') != 'true' ||
      location.href.split('?id=')[1] == undefined
    ) {
      this.$router.go(-1)
    } else {
      const id = location.href.split('?id=')[1]
      this.axios
        .get('tatuajes/listar/' + id)
        .then((res) => {
          this.tatuaje = res.data
        })
        .catch((e) => {
          console.log(e.response)
        })
      this.axios
        .get('/usuarios/tatuadores')
        .then((res) => {
          this.autores = res.data
          this.insertarTatuadores(this.autores)
        })
        .catch((e) => {
          console.log(e)
        })
    }
  },
  methods: {
    alertaTrue() {
      this.mensaje.color = 'primary'
      this.mensaje.texto = 'Tatuaje editado con exito! ðŸ‘Œ'
      this.showAlert()
    },
    alertaFalse() {
      this.mensaje.color = 'warning'
      this.mensaje.texto = 'Error :('
      this.showAlert()
    },
    actualizarTatuaje(item) {
      this.axios
        .put('tatuajes/actualizar/' + item._id, item)
        .then((res) => {
          this.tatuaje = res.data
          this.alertaTrue()
        })
        .catch((e) => {
          this.alertaFalse()
        })
    },
    insertarTatuadores(tatuadores) {
      document.getElementById('seleccion').innerHTML =
        "<option value=''>Desconocido</option>"
      for (var i = 0; i < tatuadores.length; i++) {
        if (tatuadores[i]._id == this.tatuaje.autor) {
          document.getElementById('seleccion').innerHTML +=
            `
          <option value="` +
            tatuadores[i]._id +
            `" selected>` +
            tatuadores[i].nombre +
            `</option>`
        } else {
          document.getElementById('seleccion').innerHTML +=
            `
          <option value="` +
            tatuadores[i]._id +
            `">` +
            tatuadores[i].nombre +
            `</option>`
        }
      }
    },
    subirFile(e) {
      var imagenNueva
      var file = e.target.files[0]
      const types = ['image/jpg', 'image/jpeg', 'image/png']
      if (types.includes(file.type)) {
        this.tatuaje.imagen = file
      }
      const formdata = new FormData()
      formdata.append('imagen', file)
      this.axios
        .post('/tatuajes/agregarFoto', formdata)
        .then((res) => {
          this.tatuaje.imagenes.push(res.data)
          imagenNueva = res.data
        })
        .catch((e) => {
          this.alertaFalse()
        })
        .finally((e) => {
          this.tatuaje.imagen = imagenNueva
          this.axios
            .put('tatuajes/nuevaImagen/' + this.tatuaje._id, this.tatuaje)
            .then((res) => {
              
            })
            .catch((e) => {
              this.alertaFalse()
            })
        })
    },
    eliminarImagen(item) {
      this.tatuaje.imagen = item
      this.axios
        .put('tatuajes/borrarImagen/' + this.tatuaje._id, this.tatuaje)
        .then((res) => {
          window.location.href = window.location.href
        })
        .catch((err) => {
          this.alertaFalse()
        })
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown
      if (this.mensaje.color == 'primary') {
        setTimeout(() => {
          window.location.href = '/'
        }, 5000)
      }
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs
    },
    showMsgBox(item) {
      this.boxTwo = ''
      this.$bvModal
        .msgBoxConfirm('Â¿Quieres eliminarlo?', {
          title: 'Por favor, confirme',
          size: 'sm',
          buttonSize: 'sm',
          okVariant: 'danger',
          okTitle: 'ELIMINAR',
          cancelTitle: 'CANCELAR',
          footerClass: 'p-2',
          hideHeaderClose: false,
          backgroundColor: 'success',
          centered: true,
        })
        .then((value) => {
          this.box = value
          if (this.box == true) {
            this.eliminarImagen(item)
          }
        })
        .catch((err) => {
          console.log(err)
        })
    },
    setFile(files) {
      var imagenNueva
      var file = files[0]
      const types = ['image/jpg', 'image/jpeg', 'image/png']
      if (types.includes(file.type)) {
        this.tatuaje.imagen = file
      }
      const formdata = new FormData()
      formdata.append('imagen', file)
      this.axios
        .post('/tatuajes/agregarFoto', formdata)
        .then((res) => {
          this.tatuaje.imagenes.push(res.data)
          imagenNueva = res.data
        })
        .catch((e) => {
          this.alertaFalse()
        })
        .finally((e) => {
          this.tatuaje.imagen = imagenNueva
          this.axios
            .put('tatuajes/nuevaImagen/' + this.tatuaje._id, this.tatuaje)
            .then((res) => {})
            .catch((e) => {
              this.alertaFalse()
            })
        })
    },
  },
  mounted: function () {
    const drop = document.querySelector('.drop')
    const text = document.querySelector('.text')

    drop.addEventListener('dragover', (e) => {
      e.preventDefault()
      text.innerHTML = 'Suelte el mouse para que caiga'
      drop.classList.add('active')
    })

    drop.addEventListener('dragleave', (e) => {
      e.preventDefault()
      text.innerHTML = 'Arrastra tus fotos aquÃ­! ðŸ‘‡'
      drop.classList.remove('active')
    })

    drop.addEventListener('drop', (e) => {
      e.preventDefault()
      drop.classList.remove('active')
      this.setFile(e.dataTransfer.files)
    })
  },
}
</script>

<style>
#app > div:nth-child(2) > div:nth-child(4) > form > div:nth-child(1) {
  align-items: center;
}
/* Formulario */
#form {
  background-color: #82a5c7;
  color: #243241;
  border-radius: 20px;
  height: 400px;
  min-width: 290px;
  padding-bottom: 10px;
  margin: 0 20px 0 20px !important;
}
@media (max-width: 686px) {
  #form {
    max-width: 338px;
  }
}
input {
  background-color: #4e647a !important;
  color: #82a5c7 !important;
}
select {
  background-color: #4e647a !important;
  color: #82a5c7 !important;
}
span#apend {
  background-color: #243241 !important;
  color: #82a5c7 !important;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
#form > textarea {
  background-color: #4e647a !important;
  color: #82a5c7 !important;
}
#enviar {
  background-color: #82a5c7;
  color: #243241;
}

/* Animacio borrar foto */
.imagenTattoo,
#inputImagenes {
  width: 420px;
  height: 300px;
  position: relative;
  object-fit: cover;
  z-index: 1;
}
#inputImagenes {
  position: relative;
  top: -10px;
  left: -10px;
  border-radius: 25px;
}
.xTattoo {
  z-index: 0;
  position: relative;
  height: 50px;
  width: 50px;
  top: -290px;
  left: 175px;
}
.imagenTattoo {
  border-radius: 20px;
}
.cajaTattoo {
  margin: 10px;
  height: 310px;
  width: 430px;
}
.cajaTattoo:hover .xTattoo {
  z-index: 2;
}
.cajaTattoo:hover {
  border: 5px solid red;
  border-radius: 25px;
}

/* Drag & Drop */
.drop-container {
  background: #82a5c7;
  padding: 1.5rem;
  max-width: 420px;
  height: 400px;
  display: inline-block;
  border-radius: 20px;
}
@media (max-width: 686px) {
  .drop-container {
    max-width: 400px;
  }
}
.drop {
  border-radius: 10px;
  border: 3px dotted #4e647a;
  display: grid;
  width: 100%;
  height: calc(100% - 3px);
  place-content: center;
  padding: 2rem;
  box-sizing: border-box;
  display: grid;
  place-items: center;
}
.active {
  border: 3px solid #4e647a;
  background: #42b983;
}
.drop svg {
  color: #243241 !important;
}
.drop .text {
  text-align: center;
  font-weight: 500;
  color: #243241;
  margin-top: 1rem;
}
.drop label {
  background: #243241;
  padding: 0.7rem;
  border-radius: 5px;
  color: #82a5c7;
  cursor: pointer;
}
.drop label:hover {
  transform: scale(1.1);
  box-shadow: 0 0 20px 3px rgba(2, 0, 15, 0.2);
  color: #42b983;
}
.file-input {
  display: none;
}
.line {
  width: 80px;
  height: 1px;
  background: #243241;
}
.or-con {
  display: flex;
  align-items: center;
  margin: 0.5rem;
}
.or {
  margin: 0 1rem;
  font-weight: 500;
  color: #243241;
}

@media (max-width: 770px) {
  .imagenes * {
    width: 100%;
    height: 300px;
  }
}
@media (max-width: 991px) {
  #mostrarImagenes {
    justify-content: center;
  }
}
</style>
